@font-face {
  font-family: "Montserrat-Regular";
  src: url('/assets/fonts/Montserrat/Montserrat-Regular.eot'); /* IE9 Compat Modes */
  src: url('/assets/fonts/Montserrat/Montserrat-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/assets/fonts/Montserrat/Montserrat-Regular.woff') format('woff'), /* Pretty Modern Browsers */
       url('/assets/fonts/Montserrat/Montserrat-Regular.ttf')  format('truetype'), /* Safari, Android, iOS */

}

@font-face {
  font-family: "PlayfairDisplay-Regular";
  src: url('/assets/fonts/PlayfairDisplay/PlayfairDisplay-Regular.eot'); /* IE9 Compat Modes */
  src: url('/assets/fonts/PlayfairDisplay/PlayfairDisplay-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/assets/fonts/PlayfairDisplay/PlayfairDisplay-Regular.woff') format('woff'), /* Pretty Modern Browsers */
       url('/assets/fonts/PlayfairDisplay/PlayfairDisplay-Regular.ttf')  format('truetype'), /* Safari, Android, iOS */
       
}